<template>
  <v-app>
    <v-main>
      <v-container class="text-center">
        <h1>Custom QR Code Scanner</h1>

        <!-- Nút để bắt đầu quét -->
        <v-btn color="primary" large @click="startScanning">
          Start Scanning
        </v-btn>

        <!-- Quét mã QR -->
        <qrcode-stream
          v-if="showScanner"
          @decode="onDecode"
          @init="onInit"
          :paused="paused"
          class="mt-4"
          :facingMode="'environment'"
        ></qrcode-stream>

        <!-- Hiển thị kết quả quét -->
        <v-alert v-if="decodedText" type="success" class="mt-4">
          Decoded: {{ decodedText }}
        </v-alert>

        <!-- Hiển thị lỗi -->
        <v-alert v-if="errorMessage" type="error" class="mt-4">
          {{ errorMessage }}
        </v-alert>

        <!-- Nút Restart Scanner -->
        <v-btn class="mt-4" color="primary" @click="stopScanner">
          Stop Scanning
        </v-btn>
        <input
          type="file"
          accept="image/*"
          @change="onFileChange"
          class="mt-4"
        />
        
        <!-- Hiển thị ảnh đã chọn -->
        <div v-if="selectedImage" class="mt-4">
          <img :src="selectedImage" alt="QR Code Image" class="preview-image"/>
        </div>

      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import { QrcodeStream } from "vue3-qrcode-reader";

export default {
  components: { QrcodeStream },
  data() {
    return {
      decodedText: "",
      errorMessage: "",
      showScanner: false,
      paused: false,
    };
  },
  methods: {
    // Bắt đầu quét mã QR
    startScanning() {
      this.showScanner = true;
      this.errorMessage = "";
      this.decodedText = "";
    },
    // Khi quét thành công
    onDecode(decodedString) {
      this.decodedText = decodedString;
      this.errorMessage = "";
      this.paused = true; // Tạm dừng quét sau khi quét thành công
    },
    // Khi khởi tạo camera
    onInit(promise) {
      promise.catch(() => {
        this.errorMessage =
          "Unable to access camera. Please allow camera permissions or check your browser settings.";
      });
    },
    // Dừng quét mã QR
    stopScanner() {
      this.paused = false;
      this.decodedText = "";
      this.errorMessage = "";
    },
  },
};
</script>

<style scoped>
@import "vuetify/dist/vuetify.min.css";
</style>
